.App {
  text-align: center;
  scrollbar-color: rgba(155, 155, 155, 0.5) transparent;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #1f1f1f;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #ddd;
}

button,
select,
input {
  background-color: #2a2a2a;
  color: #ddd;
  border: 1px solid #555;
  padding: 4px 8px;
  border-radius: 4px;
  margin: 2px;
  transition: background-color 0.2s, border-color 0.2s;
}

button:hover,
select:hover {
  background-color: #3a3a3a;
}

input[type="checkbox"] {
  accent-color: #0af;
}

.toggle-button {
  background-color: #2a2a2a;
  color: #ddd;
  border: 1px solid #555;
  padding: 4px 8px;
  border-radius: 4px;
  margin: 2px;
  transition: background-color 0.2s, border-color 0.2s;
}

.toggle-button:hover {
  background-color: #3a3a3a;
}

.toggle-button.active {
  background-color: #4caf50;
  color: #fff;
  border-color: #4caf50;
}

.toggle-buutton.active:hover {
  background-color: #45a049;
}